import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import {
  navBar,
  mainBody,
  about,
  repos,
  leadership,
  skills,
  getInTouch,
} from "./editable-stuff/config.js";
import MainBody from "./components/home/MainBody";
import AboutMe from "./components/home/AboutMe";
import Project from "./components/home/Project";
import Footer from "./components/Footer";
import Navbar from "./components/Navbar";
import Skills from "./components/home/Skills";
// import { Blog } from "./components/blog/Blog";
// import BlogPost from "./components/blog/BlogPost";
import GetInTouch from "./components/home/GetInTouch.jsx";
import Leadership from "./components/home/Leadership.jsx";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";

const Home = React.forwardRef((props, ref) => {
  return (
    <>
      <MainBody
        gradient={mainBody.gradientColors}
        title={`${mainBody.firstName} ${mainBody.middleName} ${mainBody.lastName}`}
        message={mainBody.message}
        icons={mainBody.icons}
        ref={ref}
      />
      <div className="bg-light m-0 jumbotron jumbotron-fluid pb-0">
        <div className="container">
          <h2 className="display-4 pb-5 text-center">Freelance Projects</h2>
          <div className="row">
            <Col md={12}>
              <Card className="card shadow-lg p-3 mb-5 bg-white rounded">
                <Card.Body>
                  <Card.Title as="h5">Zait Zatar</Card.Title>
                  <Card.Text>A food delivery app that provides a solution for restaurants owners, a store can list products, create offers, receive orders, and assign delivery. Users can login using phone authentication, select location, search for stores and products, add to their favorite list, add to cart, place orders and track orders.</Card.Text>
                  <a href="https://play.google.com/store/apps/details?id=com.sts.zaitzatar" target=" _blank" className="btn btn-outline-secondary">
                    <i class="fab fa-google-play"></i> Play Store
                  </a>
                  <hr />
                  <div>Tech Stack: <p class="badge badge-light card-link" >React Native</p><p class="badge badge-light card-link" >Firebase</p></div>
                </Card.Body>
              </Card>
            </Col>
          </div>
        </div>
      </div>
      {repos.show && (
        <Project
          heading={repos.heading}
          username={repos.gitHubUsername}
          length={repos.reposLength}
          specfic={repos.specificRepos}
        />
      )}
      {skills.show && (
        <Skills
          heading={skills.heading}
          hardSkills={skills.hardSkills}
          softSkills={skills.softSkills}
        />
      )}
      {about.show && (
        <AboutMe
          heading={about.heading}
          message={about.message}
          link={about.imageLink}
          imgSize={about.imageSize}
          resume={about.resume}
        />
      )}
      {leadership.show && (
        <Leadership
          heading={leadership.heading}
          message={leadership.message}
          img={leadership.images}
          imageSize={leadership.imageSize}
        />
      )}
    </>
  );
});

const App = () => {
  const titleRef = React.useRef();

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL + "/"}>
      <span id='home'></span>
      {navBar.show && <Navbar ref={titleRef} />}
      <Route path="/" exact component={() => <Home ref={titleRef} />} />
      {/* {false && <Route path="/blog" exact component={Blog} />}
      {false && <Route path="/blog/:id" component={BlogPost} />} */}
      <Footer>
        {getInTouch.show && (
          <GetInTouch
            heading={getInTouch.heading}
            message={getInTouch.message}
            email={getInTouch.email}
            phone={getInTouch.phone}
          />
        )}
      </Footer>
    </BrowserRouter>
  );
};

export default App;
